import React, {useEffect, useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {get} from "lodash"
import downloadIcon from "../../../assets/icons/download.svg"
import filterIcon from "../../../assets/icons/filter.svg"
import Select from "../../../components/select";
import dayjs from "dayjs";
import {MONTHS, REGIONS, REPORT_TYPES, YEARS} from "../../../constants";
import {getDefaultValue} from "../../../utils";
import {useNavigate} from "react-router-dom";
import {NumericFormat} from "react-number-format";
import cloudIcon from "../../../assets/icons/Folders.svg";
import monitorIcon from "../../../assets/icons/monitor.svg";
import {usePostQuery} from "../../../hooks/api";
import {OverlayLoader} from "../../../components/loader";
import {useTranslation} from "react-i18next";

const columns = [
    {
        title: 'ГРУППА/НАЗВАНИЕ',
        key: 'name',
        render: ({row, value}) => <div>{value} <span className={'text-[#F25886] italic'}>({get(row, 'code')})</span>
        </div>
    },
    {
        title: 'КОЭФФИЦИЕНТ',
        key: 'ratio',
    },
    {
        title: 'СЛУЧАИ ФГМС',
        key: 'fgms_count',
        render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
    },
    {
        title: 'СУММА ДЛЯ ОПЛАТЫ',
        key: 'fgms_payment',
        render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
    },
    // {
    //     title: 'ДРУГИЕ СЛУЧАИ',
    //     key: 'total_count',
    //     render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
    // },
    {
        title: 'ОПЕРАЦИИ',
        key: 'surgery_form_count',
        render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
    },
    {
        title: 'СУММА',
        key: 'total_payment',
        render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
    },
    {
        title: 'ДЕЙСТВИЯ',
        key: 'organization_level',
        classnames: '!text-center',
        render: ({row}) => <div className={'flex justify-around items-center px-3'}>
            <img className={'mr-1'} src={cloudIcon} alt="cloud"/>
            <img src={monitorIcon} alt="cloud"/>
        </div>
    }

]
const InpatientReportsByOrganizationsFgmsContainer = ({
                                                          year = parseInt(dayjs().format('YYYY')),
                                                          month = dayjs().format('MM'),
                                                          group = 1,
                                                      }) => {
    const {t} = useTranslation()
    const navigate = useNavigate()
    const [filter, setFilter] = useState({
        year, month, group
    });
    const {mutate: generateReportRequest, isLoading: isLoadingReport} = usePostQuery({listKeyId: KEYS.reports})
    useEffect(() => {
        setFilter(({year: getDefaultValue(YEARS, year), month: getDefaultValue(MONTHS, month)}))
    }, [year, month, group])

    const generateReport = () => {
        generateReportRequest({
            url: URLS.drg_report,
            attributes: {
                filters: {
                    organization_id: group,
                    month: `${year}-${month}`
                }
            }
        }, {
            onSuccess: () => {
                navigate('/reports')
            }
        })
    }
    const generateReport66 = () => {
        generateReportRequest({
            url: URLS.form_66_report,
            attributes: {
                filters: {
                    organization_id: group,
                    month: `${year}-${month}`
                }
            }
        }, {
            onSuccess: () => {
                navigate('/reports')
            }
        })
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title> {t('Расчеты по пролеченным случаям в разрезе учреждений')} </Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={generateReport}
                        className={'inline-flex mr-5 items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t('Экспорт по группам')}
                    </button>
                    <button
                        onClick={generateReport66}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t('Экспорт форма 066')}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    {isLoadingReport && <OverlayLoader/>}
                    <GridView
                        onRowClick={(_row) => navigate(`/hospitals/organizations/groups/form66/${year}/${month}/${group}/${get(_row, 'id', 1)}`)}
                        filters={<div className={'grid grid-cols-12 mb-5 gap-x-8'}>
                            <div className="col-span-3">
                                <Select
                                    setValue={(val) => setFilter((prev) => ({...prev, month: val}))}
                                    label={'Учреждение'} options={[]}/>
                            </div>
                            <div className="col-span-3 flex items-center">
                                <div className="mr-5 w-full"><Select
                                    setValue={(val) => setFilter((prev) => ({...prev, month: val}))}
                                    label={'Название'} options={[]}/></div>
                                <img
                                    onClick={() => navigate(`/hospitals/organizations/${year}/${month}/1`)}
                                    className={'cursor-pointer translate-y-1/2'} src={filterIcon} alt="filter"/>
                            </div>
                        </div>} listKey={KEYS.inpatientReports}
                        url={URLS.inpatientReports + `/${get(filter, 'year.value', year)}${get(filter, 'month.value', month)}/organizations/${group}/groups/fgms`}
                        columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default InpatientReportsByOrganizationsFgmsContainer;